"use client"

import React from "react"
import { usePathname, useSearchParams } from "next/navigation"
import Link from "next/link"
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb"
import { navigationData } from "@/lib/mock-data/navigation"

interface Media {
  id: string
  name: string
}

interface DynamicBreadcrumbProps {
  mediaData: Media[]
}

export function DynamicBreadcrumb({ mediaData }: DynamicBreadcrumbProps) {
  const pathname = usePathname()
  const searchParams = useSearchParams()
  const mediaId = searchParams.get("media")

  const getMediaName = () => {
    if (!mediaId || mediaId === "all") {
      return ""
    }
    return mediaData.find((m) => m.id === mediaId)?.name || ""
  }

  const getPageTitle = (url: string) => {
    const navItem = navigationData.find((item) => item.url === url)
    return navItem?.title || ""
  }

  const renderFinancialsBreadcrumb = () => (
    <>
      <BreadcrumbItem>
        <BreadcrumbLink asChild>
          <Link href="/dashboard"></Link>
        </BreadcrumbLink>
      </BreadcrumbItem>
      <BreadcrumbSeparator />
      <BreadcrumbItem>
        <BreadcrumbPage>{getPageTitle(pathname)}</BreadcrumbPage>
      </BreadcrumbItem>
      <BreadcrumbSeparator />
      <BreadcrumbItem>
        <BreadcrumbPage>{getMediaName()}</BreadcrumbPage>
      </BreadcrumbItem>
    </>
  )

  const renderDashboardBreadcrumb = () => (
    <>
      <BreadcrumbItem>
        <BreadcrumbLink asChild>
          <Link href="/dashboard"></Link>
        </BreadcrumbLink>
      </BreadcrumbItem>
      <BreadcrumbSeparator />
      <BreadcrumbItem>
        <BreadcrumbPage></BreadcrumbPage>
      </BreadcrumbItem>
    </>
  )

  const renderDefaultBreadcrumb = () => (
      <BreadcrumbItem>
        <BreadcrumbPage></BreadcrumbPage>
      </BreadcrumbItem>
  )


  return (
    <Breadcrumb>
      <BreadcrumbList>
        {pathname === "/dashboard/financials" && renderFinancialsBreadcrumb()}
        {pathname === "/dashboard" && renderDashboardBreadcrumb()}
        {pathname !== "/dashboard" && pathname !== "/dashboard/financials" && renderDefaultBreadcrumb()}
      </BreadcrumbList>
    </Breadcrumb>
  )
}
